var classNames = require('classnames');
var React = require('react');

var ConfigFieldTextArea = React.createClass({
  handleOnChange: function(ev) {
    if (this.props.handleChange) {
      this.props.handleChange(this.props.name, ev.target.value);
    }
  },

  render: function() {
    var inputClasses = classNames('form-control', {
      'readonly': this.props.readOnly,
    });

    return (
      <div className="field field-type-text">
        <label>{this.props.title}</label>
        <div className="input input-type-text">
          <textarea
            {...this.props.props}
            placeholder={this.props.default}
            readOnly={this.props.readOnly}
            onChange={this.handleOnChange}
            value={this.props.value}
            className={inputClasses}></textarea>
        </div>
      </div>
    );
  },
});

module.exports = ConfigFieldTextArea;
